const styles = {
  // fill colour for all main shapes, to be passed to draw()
  shapeFill: '#808080',
  // border/outline styles for all main shapes, to be passed to outline()
  outlines: {
    weight: 3,
    colour: 'RebeccaPurple'
  }
}

module.exports = styles;



